  padding: 1rem;
  text-decoration: none;
  border-radius: 99px;
  font-weight: 500;
  font-size: 20px;
}

.dashboard_container {
  /* outline: 1px solid red; */
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  width: 100%;
  height: 100%;
}

.top20Table {
  padding: 2rem;
