  display: inherit;
  /* grid-template-columns: repeat(auto-fit, minmax(60rem, 90rem)); */
  grid-gap: 2rem;
  justify-content: center;
  padding: 1rem;
  width: 95%;
  height: 50%;
}
.topResults {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  grid-row-gap: 20px;

  row-gap: 20px;


  /* max-width: 90%; */
}

.top20Row {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  background-color: #424242;
  flex-direction: row;
  /* background-color: #204064; */
  border-radius: 100px;
  flex-basis: auto;
  box-shadow: 5px 5px 20px 0.5px rgba(0, 0, 0, 0.329);
  padding: 2rem;
}
@media (max-width: 600px) {
  .top20Row {
    flex-direction: column;
    align-items: stretch;
  }
}
/* > means must be a direct decendant of top20Row and * means everything */
.top20Row > * {
  flex-grow: 1;
  align-self: center;
}
@media (max-width: 600px) {
  .top20Row > * {
    max-width: 50%;
  }
}
@media (min-width: 601px) {
  /* .top20Row {
    max-height: 170px;
